.about-disciplines {
  position: relative;
  overflow: hidden;
  margin-top: 10rem;
}
.about-disciplines::before {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  z-index: 2;
  width: 17.5rem;
  height: 77%;
  background: linear-gradient(90deg, #f2f2f2 0%, rgba(242, 242, 242, 0) 100%);
  pointer-events: none;
}
@media (max-width: 480px) {
  .about-disciplines::before {
    width: 2.5rem;
  }
}
.about-disciplines::after {
  content: "";
  position: absolute;
  bottom: 0;
  right: 0;
  z-index: 2;
  width: 17.5rem;
  height: 77%;
  transform: rotate(180deg);
  background: linear-gradient(90deg, #f2f2f2 0%, rgba(242, 242, 242, 0) 100%);
  pointer-events: none;
}
@media (max-width: 480px) {
  .about-disciplines::after {
    width: 2.5rem;
  }
}

.about-disciplines__header {
  display: flex;
  justify-content: space-between;
}
@media (max-width: 768px) {
  .about-disciplines__header {
    flex-direction: column;
    gap: 1rem;
  }
}

.about-disciplines__head {
  display: flex;
  justify-content: space-between;
}
@media (max-width: 768px) {
  .about-disciplines__head {
    gap: 1rem;
  }
}
@media (max-width: 480px) {
  .about-disciplines__head {
    gap: 0.5rem;
  }
}

@media (max-width: 480px) {
  .about-disciplines__svg {
    width: 3rem;
    height: 3rem;
  }
}

@media (max-width: 480px) {
  .about-disciplines__button {
    width: 100%;
  }
}

@media (max-width: 480px) {
  .about-disciplines__link {
    width: 100%;
    text-align: center;
  }
}

.about-disciplines__slider {
  position: relative;
  padding-bottom: 5rem;
  margin-top: 2.5rem;
}
.about-disciplines__slider .about-disciplines__pagination {
  width: 80%;
  bottom: 1.5rem;
  top: auto;
  left: 4rem;
  border-radius: 2.5rem;
  background-color: rgba(180, 180, 180, 0.2);
  left: 50%;
  transform: translateX(-50%);
}
@media (max-width: 768px) {
  .about-disciplines__slider .about-disciplines__pagination {
    width: 70%;
  }
}
.about-disciplines__slider .about-disciplines__pagination .swiper-pagination-progressbar-fill {
  background-color: var(--color-red);
  border-radius: 2.5rem;
}
.about-disciplines__slider .about-disciplines__button-prev {
  top: auto;
  bottom: 0;
  transform: rotate(90deg);
  color: var(--color-red);
  width: 3.125rem;
  height: 3.125rem;
  display: flex;
  align-items: center;
  justify-content: center;
}
.about-disciplines__slider .about-disciplines__button-prev:hover {
  transform: scale(1.25) rotate(90deg);
}
.about-disciplines__slider .about-disciplines__button-prev:active {
  transform: scale(1.05) rotate(90deg);
}
.about-disciplines__slider .about-disciplines__button-prev::after {
  content: none;
}
.about-disciplines__slider .about-disciplines__button-next {
  top: auto;
  bottom: 0;
  transform: rotate(-90deg);
  color: var(--color-red);
  width: 3.125rem;
  height: 3.125rem;
  display: flex;
  align-items: center;
  justify-content: center;
}
.about-disciplines__slider .about-disciplines__button-next:hover {
  transform: scale(1.25) rotate(-90deg);
}
.about-disciplines__slider .about-disciplines__button-next:active {
  transform: scale(1.05) rotate(-90deg);
}
.about-disciplines__slider .about-disciplines__button-next::after {
  content: none;
}
.about-disciplines__slider .about-disciplines__button-prev,
.about-disciplines__slider .about-disciplines__button-next {
  transition: transform 0.2s ease-in-out;
}

.about-disciplines__slide {
  max-width: 19.375rem;
}

.about-disciplines__card {
  display: block;
  overflow: hidden;
  position: relative;
  padding: 1.5rem;
  min-height: 20.625rem;
  background-color: var(--light-color);
  border-radius: 1rem;
  transition: transform 0.3s ease-in-out;
}
.about-disciplines__card:hover {
  transform: scale(1.05);
}
.about-disciplines__card:hover .about-disciplines__subtitle {
  color: var(--color-red);
}

.about-disciplines__subtitle {
  font-size: 2rem;
  font-weight: 700;
  line-height: 1.2;
  transition: color 0.3s ease-in-out;
}
@media (max-width: 480px) {
  .about-disciplines__subtitle {
    font-size: 1.5rem;
  }
}

.about-disciplines__icon {
  flex-shrink: 0;
}

.about-disciplines__picture {
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  bottom: 0;
  right: 0;
  min-height: 11.3125rem;
  overflow: hidden;
  transition: transform 0.3s ease-in-out;
}
.about-disciplines__picture_center {
  width: 100%;
}
.about-disciplines__picture:hover {
  transform: scale(1.2);
}

.about-disciplines__cover {
  display: block;
  object-fit: cover;
}